<template>
  <div>
    <Row class="user-info">
      <Col span="6" class="header-img">
        <div class="img-box">
          <img src="@/assets/images/ximg15.png" alt="" />
        </div>
        <p class="name">{{getUserInfo.mobile}}</p>
        <p class="already-auth"><Icon type="md-checkmark-circle-outline" color="#dc5535" :size="28" class="mr8" />已认证</p>
      </Col>
      <Col span="18">
        <Row class="wallet">
          <Col span="8">
            <p class="num">1000.00</p>
            <p class="tip">账户余额</p>
            <p class="operation">充值</p>
          </Col>
          <Col span="8">
            <p class="num">1000.00</p>
            <p class="tip">佣金余额</p>
            <p class="operation">提现</p>
          </Col>
          <Col span="8">
            <p class="num">1000.00</p>
            <p class="tip">冻结余额</p>
          </Col>
        </Row>
      </Col>
    </Row>
    <div class="my-order">
      <p class="tip">我的订单</p>

      <Row>
        <Col span="2">&nbsp;</Col>
        <Col span="4" class="item">
          <router-link to tag="div">
            <p>
              <Icon :size="40" type="ios-folder-open-outline" />
            </p>
            <p>待付款</p>
          </router-link>
        </Col>
        <Col span="4" class="item">
          <router-link to tag="div">
            <p>
              <Icon :size="40" type="ios-checkbox-outline" />
            </p>
            <p>待收货</p>
          </router-link>
        </Col>
        <Col span="4" class="item">
          <router-link to tag="div">
            <p>
              <Icon :size="40" type="ios-chatboxes-outline" />
            </p>
            <p>待评价</p>
          </router-link>
        </Col>
        <Col span="4" class="item">
          <router-link to tag="div">
            <p>
              <Icon :size="40" type="ios-sync" />
            </p>
            <p>退换/售后</p>
          </router-link>
        </Col>
        <Col span="4" class="item">
          <router-link to tag="div">
            <p>
              <Icon :size="40" type="ios-clipboard-outline" />
            </p>
            <p>全部订单</p>
          </router-link>
        </Col>
        <Col span="2">&nbsp;</Col>
      </Row>
      <p class="empty-shopping"><Icon type="md-cart" :size="80" />您的购物车还是空的，快去购买合适的商品吧！</p>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["getUserInfo"])
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/theme.less";
@import "~@/less/mixins.less";
.my-order {
  background-color: #fff;
  .tip {
    padding: 10px;
    border-bottom: 1px solid @borderColor;
  }
  .item {
    padding: 20px 0;
    font-size: 12px;
    text-align: center;
    line-height: 32px;
    cursor: pointer;
    &:hover {
      color: @themeColor;
    }
  }
  .empty-shopping{
    text-align: center;
    color: #999;
    padding: 45px 0;
  }
}
.user-info{
  background-color: #fff;
  margin-bottom: 20px;
  .header-img{
    padding-top: 20px;
    text-align: center;
    .name{
      font-size: 20px;
    }
    .already-auth{
      margin-top: 34px;
    }
  }
  .img-box{
    width: 100px;
    margin: 0 auto 10px;
    img{
      .blockImg();
    }
  }
  .wallet{
    border-left: 1px solid @borderColor;
    padding: 74px 50px 70px;
    .num{
      font-size: 24px;
    }
    .tip{
      margin: 20px 0 10px;
    }
    .operation{
      color: @themeColor;
    }
  }
}
</style>



